// src/components/ChatScreen.tsx
import React, { useEffect, useRef, useState } from "react";
import { Box, Text, Button, Spinner, Heading } from "@chakra-ui/react";
import { useQuery, useQueryClient } from "react-query";
import axios from "axios";

const ChatScreen: React.FC = () => {
  const chatContainerRef = useRef<HTMLDivElement>(null);
  const queryClient = useQueryClient();
  const [isLoadingMore, setIsLoadingMore] = useState(false);

  const { data, isLoading, isError, isFetching, refetch } = useQuery(
    "chat",
    async () => {
      const response = await axios.get(
        "https://qa.corider.in/assignment/chat?page=0"
      );
      return response.data;
    },
    {
      staleTime: 0, // To always refetch on component mount
    }
  );

  const loadMoreMessages = async () => {
    setIsLoadingMore(true);
    try {
      // Fetch the next page
      await queryClient.fetchQuery(["chat"], { staleTime: 0 });
    } finally {
      setIsLoadingMore(false);
    }
  };

  useEffect(() => {
    const handleScroll = () => {
      if (chatContainerRef.current) {
        const { scrollTop, clientHeight, scrollHeight } =
          chatContainerRef.current;
        const scrollThreshold = 200;

        if (
          scrollTop === 0 &&
          !isLoading &&
          !isFetching &&
          scrollHeight > clientHeight + scrollThreshold
        ) {
          console.log("Fetching older messages...");
          // Load more messages when scrolled to the top
          loadMoreMessages();
        }
      }
    };

    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [isLoading, isFetching, queryClient]);

  if (isLoading) return <Spinner size="xl" />;

  if (isError) return <div>Error loading chat data</div>;

  const messages = data?.chats || [];

  return (
    <Box
      ref={chatContainerRef}
      h="80vh"
      overflowY="auto"
      border="1px solid #ccc"
      borderRadius="md"
      p={4}
      bg="#f4f4f4"
      color="#333"
      fontFamily="Arial, sans-serif"
    >
      <Heading as="h2" mb={4} fontSize="2xl" textAlign="center">
        Chat Messages
      </Heading>

      {messages.map((message: any) => (
        <Box
          key={message.id}
          mb={4}
          p={3}
          boxShadow="md"
          borderRadius="lg"
          bg="white"
        >
          <Text fontSize="lg">{message.message}</Text>
          <Text fontSize="sm" color="gray.500" mt={2}>
            {message.time}
          </Text>
        </Box>
      ))}

      {messages.length === 0 && (
        <Box textAlign="center" color="gray.500" mt={4}>
          No messages available.
        </Box>
      )}

      <Box mt={4} textAlign="center">
        <Button
          onClick={loadMoreMessages}
          isLoading={isLoadingMore}
          loadingText="Loading more messages"
          colorScheme="teal"
          size="lg"
          fontSize="md"
        >
          Load More
        </Button>
      </Box>
    </Box>
  );
};

export default ChatScreen;
